<template>
  <div class="big-table-wrap" ref="tableWrap">
    <div class="search-wrap" ref="searchWrap">
      <Row :gutter="16">
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">积分：</span>
            <Input v-model="point" placeholder="积分" class="input-width"></Input>
          </div>
        </Col>
        <Col span="6">
          <div  class="input-item">
            <span class="input-label">消费者账号：</span>
            <Input v-model="userNumber" placeholder="消费者账号" class="input-width"></Input>
          </div>
        </Col>
       <Col span="6">
          <div  class="input-item">
            <span class="input-label">积分账户：</span>
            <Input v-model="pointsAccount" placeholder="积分账户" class="input-width"></Input>
          </div>
        </Col>
        <Col span="10">
          <div  class="input-item">
            <daterange :name="'绑定日期'" ref="regTime" @start-time="timeChange('bindStart',$event)"  @end-time="timeChange('bindEnd',$event)"></daterange>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item">
            <Button type="primary" @click="reSearchInfo">查询</Button>
          </div>
        </Col>
        <Col span="2">
          <div  class="input-item right" >
            <Button type="primary" @click="resetSearch" >重置</Button>
          </div>
        </Col>
      </Row>
    </div>
    <!--操作按钮区域-->
    <div class="handle-wrap" ref="handleWrap">
      <div class="handle-btn-wrap">
        <Button type="primary" @click="exportInfo">导出</Button>
      </div>
    </div>
    <!--table表格区域-->
    <div class="table-wrap-style" ref="tableBox">
      <Table  :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns" stripe border></Table>
    </div>
    <div class="page-wrap" ref="pageWrap">
      <div class="right">
        <Page :total="total" :current="page" @on-change="changePage" @on-page-size-change="changeSize" show-total show-sizer :page-size-opts="pageSizeOpts"></Page>
      </div>
    </div>
  </div>
</template>
<script>
  import { tableMethods } from "../../../js/mixintable";
  import daterange from '../../components/daterange'
  export default {
    name: "BindQuery",//积分绑定
    data () {
      return {
        /*搜索区域 条件*/
        point:'', //积分
        userNumber:'',//消费者账号
        pointsAccount:'',//积分账户
        bindStartTime:'', //绑定开始时间
        bindEndTime:'', //绑定结束时间
        loading:false,
        /*table表格*/
        tableColumns: [
          {
            title: '消费者账号',
            key: 'user_no',
            align: 'center',
            minWidth: 100,
          },
          {
            title: '积分ID',
            align: 'center',
            key: 'point_cv',
            minWidth: 100,
          },
          {
            title: '积分名称',
            align: 'center',
            key: 'point_name',
            minWidth: 100,
          },
          {
            title: '积分账户',
            align: 'center',
            key: '',//空着
            minWidth: 100,
          },
          {
            title: '绑定时间',
            key: 'op_time',
            align: 'center',
            minWidth: 100,
          }
        ]
      }
    },
    components: {
      daterange
    },
    mixins: [tableMethods],
    mounted() {
      this.$nextTick(function () {
        //查询数据
        // this.searchInfo();
      })
    },
    methods: {
      //重置查询条件
      resetSearch(){
        /*搜索区域 条件*/
        this.point=''// 积分
        this.userNumber='' //消费者账号
        this.pointsAccount=''//积分账户
        this.$refs.regTime.resetTime()
        this.bindStartTime='' //绑定开始时间
        this.bindEndTime='' //绑定结束时间
      },
      //查询数据
      searchInfo(){
        let that=this
        this.loading=true;
        let objData={
          point:this.point,// 积分
          user_no:this.userNumber,//消费者账号
          // this.pointsAccount,//积分账户
          min_op_time:this.bindStartTime.length>0?this.bindStartTime.replace(/-/g,''):'',
          max_op_time:this.bindEndTime.length>0?this.bindEndTime.replace(/-/g,''):'',
          pPage:this.page,
          pSize:this.size
        };
        let jsonData = protocol.getJsonParams(protocol.QueryPointsBindBL, objData);
        http.ajax('', jsonData, true, (data) => {
          this.loading=false;
          this.$Loading.finish();
          let resultData=[]
          /*添加序号*/
          if(data.returnList&&data.returnList.length>0){
            data.returnList.forEach(function (item,index) {
              item.indexs=that.page*that.size+index-(that.size-1)
              resultData.push(item)
            })
          }
          this.tableData = resultData;
          this.total =data.total-0;
        })
      },
      //获取时间变化并赋值
      timeChange(e,time){
        if(e=="bindStart"){
          this.bindStartTime=time
        }else if(e=="bindEnd"){
          this.bindEndTime=time
        }
      },
      //导出数据
      exportOk(){
        let objData={
          point:this.point,// 积分
          user_no:this.userNumber,//消费者账号
          // this.pointsAccount,//积分账户
          min_op_time:this.bindStartTime.length>0?this.bindStartTime.replace(/-/g,''):'',
          max_op_time:this.bindEndTime.length>0?this.bindEndTime.replace(/-/g,''):'',
        };
        let jsonData = protocol.getJsonParams(protocol.exportPointsBind, objData);
        http.ajax('', jsonData, true, (data) => {
          window.location.href=loadUrl+data.excel_url
        })
      },
    }
  }
</script>
<style lang="less" scoped>
  @import '../../../css/common.css';
</style>




